export class User {
  public id: number;
  public username: string;
  public email: string;
  public password: string;
  public defaultGroupId:number;
  public groups: any[];
  public roles: any[];
  public enabled: boolean;

  constructor(id: number, username: string, email: string, password: string, groups: any[], roles: any[]) {
    this.id = id;
    this.username = username;
    this.email = email;
    this.password = password;
    this.groups = groups;
    this.roles = roles;
  }
}
